export default {
	components: {
		 
	},
	data() {
		return {
			statusBarHeight:0, 
			showWant:true,  
			searchText:'',								//搜索关键词
			hList:uni.getStorageSync('search_cache'),	//历史记录
			themeClass: 'block', 
			hotList: ['档案']	//初始化推荐列表
		}
	},
	onLoad() {
		// 获取手机状态栏高度
		this.statusBarHeight = uni.getSystemInfoSync()['statusBarHeight']; 
	},
	watch: {
		searchText: function(newVal, oldVal) {
			uni.navigateTo({
				url: '../search/search?txt='+newVal 
			});  
		}
	},
	methods: {
		goBack(){
			uni.navigateTo({
				url: '../option/option' 
			}); 
		},
		getSearchText(e) {
			uni.showToast({
				title:'回调的搜索信息: ' + e,
				icon:"none"
			})
		},
		searchStart: function() {	//触发搜索
			let _this = this;
			if (_this.searchText == '') {
				uni.showToast({
					title: '请输入关键字',
					icon: 'none',
					duration: 1000
				});
			}else{
				uni.getStorage({
					key:'search_cache',
					success(res){
						let list = res.data;
						if(list.length > 5){
							for(let item of list){
								if(item == _this.searchText){
									_this.$emit('getSearchText', _this.searchText);
									return;
								}
							}
							list.pop();
							list.unshift(_this.searchText);
						}else{
							for(let item of list){
								if(item == _this.searchText){
									return;
								}
							}
							list.unshift(_this.searchText);
						}
						_this.hList = list;
						uni.setStorage({
							key: 'search_cache',
							data: _this.hList
						});
						_this.$emit('getSearchText', _this.searchText);
					},
					fail() {
						_this.hList = [];
						_this.hList.push(_this.searchText);
						uni.setStorage({
							key: 'search_cache',
							data: _this.hList
						});
						_this.$emit('getSearchText', _this.searchText);
					}
				})
			}
		},
		keywordsClick (item) {	//推荐搜索
			this.searchText = item;
			this.searchStart();
		},
		delhistory () {		//清空历史记录
			this.hList = [];
			uni.setStorage({
				key: 'search_cache',
				data: []
			});
		}, 
	},
}
